export enum E_MapControlActions {
  setWidthRatio,
  pointerDown,
  pointerUp,
  pointerMove,
  zoomIn,
  zoomOut,
  zoomReset
}

export type T_ViewBoxProps = {
  x: number;
  y: number;
  width: number;
  height: number;
};

export type T_MapControlState = {
  zoomFactor: number;
  widthRatio: number;
  initialViewBox: T_ViewBoxProps;
  viewBox: T_ViewBoxProps;
  // for move events to check if pointer is down or not
  isPointerDown: boolean;
  // contains the original coordinates when the user start
  // pressing the mouse or touching the screen
  pointerOrigin: { x: number; y: number };
};

export type T_SVGEventType =
  | React.MouseEvent<SVGSVGElement, MouseEvent>
  | React.TouchEvent<SVGSVGElement>;

export type T_MapControlAction = [type: E_MapControlActions, payload?: any];
